import React, { useState, useEffect } from "react";
import { useHistory } from 'react-router-dom';
import "../../App.css";
import http from "../10Services/httpService";
import apiEndpoint from "../10Services/endpoint";
import swal from "sweetalert";

function KindergartenInputForm() {
  const initKindergartenData = {
    address: "",
    capacityAgeGroup2to3: 0,
    capacityAgeGroup3to6: 0,
    elderate: "",
    id: "",
    name: "",
  };

  var savingStatus = false;

  const [data, setData] = useState(initKindergartenData);
  const [elderates, setElderate] = useState([]);
  const history = useHistory();

  useEffect(() => {
    http
      .get(`${apiEndpoint}/api/darzeliai/manager/elderates`)
      .then((response) => {
        setElderate(response.data);
      })
      .catch((error) => {
        swal({
          text: "vyko klaida nuskaitant seninijas. " + error.response.data,
          button: "Gerai",
        });
      });
  }, [setElderate]);

  const handleSubmit = (event) => {
    event.preventDefault();
    //console.log("saugoma  server");
    //console.log(data);
    savingStatus = true;
    http
      .post(`${apiEndpoint}/api/darzeliai/manager/createKindergarten`, data)
      .then((response) => {
        //console.log("rayta: " + response.data);
        swal({
          text: "Naujas darelis " + data.name + " pridtas skmingai!",
          button: "Gerai",
        });
        savingStatus = false;
        resetForm(event);
        history.push("/new");
        history.replace("/darzeliai")
      })
      .catch((error) => {
        if (error.response.status === 409) {
          swal({
            text:
              "vyko klaida raant nauj darel. " +
              error.response.data +
              "\n\nPatikrinkite duomenis ir bandykite dar kart",
            button: "Gerai",
          });
        }
        savingStatus = false;
      });
  };

  const validateField = (event) => {
    const target = event.target;

    if (target.validity.valueMissing) {
      if (target.id === "elderate") {
        target.setCustomValidity("Reikia pasirinkti seninij");
      } else target.setCustomValidity("Btina upildyti  laukel");
    } else if (target.validity.patternMismatch) {
      if (target.id === "id")
        target.setCustomValidity("staigos kod turi sudaryti 9 skaitmenys");
      if (target.id === "name")
        target.setCustomValidity("Pavadinimas turi bti 3-50 simboli ir negali prasidti tarpu");
    } else if (target.validity.rangeUnderflow || target.validity.rangeOverflow) {
      target.setCustomValidity("Negali bti maiau nei 0 ir daugiau nei 999");

    } else {
      target.setCustomValidity("");
    }
  };

  const handleChange = (event) => {
    validateField(event);
   
    setData({
      ...data,
      [event.target.name]: event.target.value,
    });
    
  };

  const resetForm = (event) => {
    event.preventDefault();
    setData(initKindergartenData);
  };

  return (
    <div>
      <form onSubmit={handleSubmit} onReset={resetForm}>
        <h6 className="py-3">
          <b>Pridti nauj darel </b>
        </h6>
        <div className="form-group">
          <label htmlFor="id">
            staigos kodas <span className="fieldRequired">*</span>
          </label>
          <input
            type="text"
            className="form-control"
            name="id"
            id="id"
            value={data.id}
            onChange={handleChange}
            onInvalid={validateField}
            required
            pattern="\d{9}"
            placeholder="123456789"
            data-toggle="tooltip"
            data-placement="top"
            title="veskite staigos (darelio) kod (9 skaitmenys)"
          />
        </div>

        <div className="form-group">
          <label htmlFor="name">
            Pavadinimas <span className="fieldRequired">*</span>
          </label>
          <input
            type="text"
            className="form-control "
            name="name"
            id="name"
            value={data.name}
            onChange={handleChange}
            onInvalid={validateField}
            required
            pattern="\S[\s\S]{2,49}"
            placeholder="3-50 simboli"
            data-toggle="tooltip"
            data-placement="top"
            title="veskite darelio pavadinim (nuo 3 iki 50 simboli)"
          />
        </div>

        <div className="form-group">
          <label htmlFor="address">
            Adresas <span className="fieldRequired">*</span>
          </label>
          <input
            type="text"
            className="form-control"
            name="address"
            id="address"
            value={data.address}
            onChange={handleChange}
            onInvalid={validateField}
            required
            data-toggle="tooltip"
            data-placement="top"
            title="veskite darelio adres"
          />
        </div>

        <div className="form-group">
          <label htmlFor="elderate">
            Seninija <span className="fieldRequired">*</span>
          </label>
          <select
            type="text"
            className="form-control"
            name="elderate"
            id="elderate"
            value={data.elderate}
            onChange={handleChange}
            onInvalid={validateField}
            required
            data-toggle="tooltip"
            data-placement="top"
            title="Pasirinkite seninij, kuriai priskiriamas darelis"
          >
            <option value="" disabled hidden label="Pasirinkite" />
            {elderates.map((option) => (
              <option value={option} label={option} key={option} />
            ))}
          </select>
        </div>
        <h6 className="py-3">
          <b>Laisv viet skaiius </b>
          <span className="fieldRequired">*</span>
        </h6>
        <div className="form-group">
          <label htmlFor="capacityAgeGroup2to3">2-3 met grupse</label>
          <input
            type="number"
            min="0"
            max="999"
            className="form-control"
            name="capacityAgeGroup2to3"
            id="capacityAgeGroup2to3"
            value={data.capacityAgeGroup2to3}
            onChange={handleChange}
            onInvalid={validateField}
            required
            data-toggle="tooltip"
            data-placement="top"
            title="veskite 2-3 met amiaus grupse esani viet skaii"
          />
        </div>

        <div className="form-group">
          <label htmlFor="capacityAgeGroup3to6">3-6 met grupse</label>
          <input
            type="number"
            min="0"
            max="999"
            className="form-control"
            name="capacityAgeGroup3to6"
            id="capacityAgeGroup3to6"
            value={data.capacityAgeGroup3to6}
            onChange={handleChange}
            onInvalid={validateField}
            required
            data-toggle="tooltip"
            data-placement="top"
            title="veskite 2-3 met amiaus grupse esani viet skaii"
          />
        </div>

        <button
          type="reset"
          className="btn btn-outline-danger mr-2 form-group"
          id="btnClearKindergartenForm"
        >
          Ivalyti
        </button>
        <button
          type="submit"
          className="btn btn-primary form-group"
          id="btnSaveKindergarten"
          disabled={savingStatus}
        >
          {savingStatus ? "Pridedama..." : "Pridti"}
        </button>
      </form>
    </div>
  );
}

export default KindergartenInputForm;
